@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed|Roboto:400,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans|Open+Sans+Condensed:300&display=swap);
body {
  margin: 0;
  color: #333;
  font-family: 'Roboto', sans-serif;
}

header {
  align-items: center;
  border-bottom: 1px solid #a88fca;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0px 30px 30px;
}

@media (min-width: 768px) {
  header {
    padding: 10px 40px;
  }
}

header .name {
  font-family: 'Open Sans', sans-serif;
  font-size: 33px;
  margin: 10px 0;
}

header .tagline {
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 22px;
  margin-top: -15px;
  letter-spacing: 5px;
}

header a.button {
  border-radius: 4px;
  border: 1px solid #a88fca;
  background: #a88fca;
  color: #fff;
  padding: 10px 20px;
  margin: 0 5px;
  text-transform: uppercase;
  text-decoration: none;
}

header a.button:first-of-type {
  margin: 0;
}


header a.button:link, header a.button:visited, header a.button:hover, header a.button:active  {
  color: #fff;
}

header a.button:hover, header a.button:active {
  box-shadow: 0px 2px 5px #808080;
}

main {
  padding: 0 40px;
}

main h1 {
  font-family: 'Caveat', cursive;
  color: #a88fca;
  font-size: 40px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding: 20px 0px;
}

main p {
  margin: 0 auto 30px;
}

main img {
  box-shadow: 0px 2px 5px #808080;
}

main img:hover {
  box-shadow: 0px 2px 15px #808080;
}

@media (min-width: 768px) {
  main h1 {
    padding: 30px;
  }
  main p {
    width: 75%;
  }
}
footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 40px;
}

footer div {
  padding: 10px 0;
}

@media (min-width: 768px) {
  footer div {
    padding: 0;
  }
}

footer a {
  color: #a88fca;
  padding: 0 20px;
  text-decoration: none;
}

footer a:first-of-type {
  padding-left: 0;
}

footer a:hover {
  color: #785e9c;
}

footer a.origen {
  color: #999;
  font-size: 12px;
  padding: 0;
}

footer a.origen:hover {
  color: #555;
}
